/**
 * System imports
 ============================================================================ */
import React, { useRef } from "react";
import { useTranslation } from "react-i18next";
import { Form, Badge, Accordion, Card } from "react-bootstrap";
// import { useAccordionToggle } from "react-bootstrap/AccordionToggle";
// import { Redirect } from "react-router-dom";
// import Octicon, { ChevronRight } from "@primer/octicons-react";
// import AccessAlarmIcon from "@material-ui/icons/AccessAlarm";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import { FileCopy as FileCopyIcon } from "@material-ui/icons";

/**
 * User imports
 ============================================================================ */
import ZCard from "../card";
import Transactions from "../transactions";
import Shares from "../shares";
import AccordionCard from "../accordionCard";

export default (args) => {
  // let defaults = {};
  // const props = Object.assign(defaults, args);

  /**
   * Default Properties
   */
  let defaults = {};

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   */
  const {
    userData,
    accountNumber,
    accordionView,
    // currencyAbbr,
  } = props.barrelGetters;
  const {
    setAccountType,
    setRedirectTo,
    setAccordionView,
    // setDeleteResponseProp,
    setResponseProps,
    setClearRefs,
  } = props.barrelSetters;
  const {
    format,
    getAccountData,
    getTransactions,
    getShares,
    showMessage,
    copyToClipboard,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  // const userData = props.data.userData;
  // let format = props.format;
  const { t } = useTranslation("Balance");
  // const [redirect, setRedirect] = useState(false);

  const refAccountNumber = useRef(null);

  // console.log("BALANCE DATA:", props);
  // console.log("BALANCE userData", userData);
  // const userDataParsed = JSON.parse(userData);
  const userDataParsed = userData;

  // let footer = <small className="text-muted">Last updated 3 mins ago</small>;

  /**
   *
   * @param {*} type
   */
  const onAccountTypeSelected = (type) => {
    setAccountType(type);
    setRedirectTo("/transfer");
  };

  const available =
    userDataParsed !== null
      ? format(
          userDataParsed.acc_curr +
            userDataParsed.acc_cred +
            userDataParsed.acc_save
        )
      : null;
  let acc_curr =
    userDataParsed !== null ? format(userDataParsed.acc_curr) : null;
  let acc_cred =
    userDataParsed !== null ? format(userDataParsed.acc_cred) : null;
  let acc_save =
    userDataParsed !== null ? format(userDataParsed.acc_save) : null;

  if (accountNumber === null) setRedirectTo("/sign-out");

  /**
   *
   * @param {*} view
   */
  const toggleView = (view) => {
    const newView = accordionView !== view ? view : null;

    setResponseProps(null);
    setAccordionView(newView);
    setClearRefs(true);

    if (newView !== null) {
      switch (view) {
        case "balance":
          getAccountData();
          break;

        case "transactions":
          getTransactions(1);
          break;

        case "shares":
          // setDeleteResponseProp("get shares");
          getShares(1);
          break;

        default:
          break;
      }
    }
  };

  // const copyToClipboard = () => {
  //   refAccountNumber.current.select();
  //   document.execCommand("copy");
  //   refAccountNumber.current.blur();
  //   showMessage("COPIED_TO_CLIPBOARD", "info", false);
  // };

  return (
    <>
      <Accordion defaultActiveKey={accordionView}>
        {/**
         * Account number
         */}
        <AccordionCard
          title={t("Account number")}
          eventkey="accountNumber"
          active={accordionView}
          paddingBody="20px 0 0 0"
          onClick={() => toggleView("accountNumber")}
        >
          <>
            <input
              ref={refAccountNumber}
              className="no-focus"
              readOnly
              style={{
                border: 0,
                width: "10rem",
                background: "Transparent",
              }}
              type="text"
              defaultValue={accountNumber}
            />
            {document.queryCommandSupported("copy") && (
              <span style={{ float: "right", marginLeft: "0.25rem" }}>
                <button
                  className="button-transparent"
                  onClick={() => copyToClipboard(refAccountNumber)}
                >
                  <FileCopyIcon style={{ color: "var(--dark)" }} />
                </button>
              </span>
            )}
          </>
        </AccordionCard>

        {/**
         * Balance
         */}
        <AccordionCard
          title={t("Balance")}
          eventkey="balance"
          active={accordionView}
          paddingBody="20px 0 0 0"
          onClick={() => toggleView("balance")}
        >
          <>
            <Form noValidate>
              <Form.Group>
                <Form.Label>{t("Available")}</Form.Label>

                <Form.Text>
                  <div style={{ fontSize: "3em" }}>{available}</div>
                </Form.Text>
              </Form.Group>
              <Form.Group>
                <ul className="list-group">
                  <li className="list-group-item d-flex justify-content-between align-items-center">
                    <div style={{}}>{t("Current account")}</div>
                    <span
                      className="link"
                      onClick={() => onAccountTypeSelected("acc_curr")}
                    >
                      <Badge variant="Light">{acc_curr}</Badge>
                      &nbsp;
                      <ArrowForwardIosIcon style={{ color: "var(--dark)" }} />
                    </span>
                  </li>
                  <li className="list-group-item d-flex justify-content-between align-items-center">
                    <div style={{}}>{t("Credit account")}</div>

                    <span
                      className="link"
                      onClick={() => onAccountTypeSelected("acc_cred")}
                    >
                      <Badge variant="Light">{acc_cred}</Badge>
                      &nbsp;
                      <ArrowForwardIosIcon style={{ color: "var(--dark)" }} />
                    </span>
                  </li>
                  <li className="list-group-item d-flex justify-content-between align-items-center">
                    <div style={{}}>{t("Savings account")}</div>
                    <span
                      className="link"
                      onClick={() => onAccountTypeSelected("acc_save")}
                    >
                      <Badge variant="Light">{acc_save}</Badge>
                      &nbsp;
                      <ArrowForwardIosIcon style={{ color: "var(--dark)" }} />
                    </span>
                  </li>
                </ul>
              </Form.Group>
            </Form>
          </>
        </AccordionCard>

        {/**
         * Transactions
         */}
        <AccordionCard
          title={t("Transactions")}
          eventkey="transactions"
          active={accordionView}
          paddingBody="20px 0 0 0"
          onClick={() => toggleView("transactions")}
        >
          <Transactions {...args} />
        </AccordionCard>

        {/**
         * Shares
         */}
        <AccordionCard
          title={t("Shares")}
          eventkey="shares"
          active={accordionView}
          paddingBody="20px 0 0 0"
          onClick={() => toggleView("shares")}
        >
          <Shares {...args} />
        </AccordionCard>
      </Accordion>
    </>
  );
};
